.tableContainer {
  margin-top: 24px;
  filter: drop-shadow(1px 1px 2px rgba(0, 0, 0, 0.1)) drop-shadow(-1px -1px 2px rgba(0, 0, 0, 0.1));
  margin-bottom: 30px;


  .titles {
    background: #EFEFEF;
    padding: 16px 32px;
    display: grid;
    grid-template-columns: 2fr 2fr 1.6fr 1.4fr 1fr;

    & > span {
      font-weight: 500;
      font-size: 14px;
      line-height: 17px;
      color: #000000;
    }
  }

  .packs {


    .pack {
      display: grid;
      grid-template-columns: 2fr 2fr 1.6fr 1.4fr 1fr;
      padding: 16px 32px;
      border-right: 1px solid #D9D9D9;
      border-left: 1px solid #D9D9D9;
      border-bottom: 1px solid #D9D9D9;
      //filter: drop-shadow(1px 1px 2px rgba(0, 0, 0, 0.1)) drop-shadow(-1px -1px 2px rgba(0, 0, 0, 0.1));

      & > span {
        font-weight: 400;
        font-size: 13px;
        line-height: 16px;
        color: #000000;

      }

      .userName {
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
      }

      .iconsBox {

        & > svg {
          margin-right: 14px;
          cursor: pointer;
          width: 16px;
          height: 16px;
          color: #000000;
        }
      }

    }
  }
}